@import "../../../variables.scss";

#header{
  width: 100%;
  height: 70px;
  position: fixed;
  top: 0;
  padding: 10px;
  background-color: $mainColor;
  // z-index: 1;
}

.header--wrap{
  position: relative;
  width: 1480px;
  left: 50%;
  margin-left: -740px;
}

.header--homeBtn--wrap{
  position: absolute;
  right: 0;
}

.header--homeBtn--img{
  @include backgroundImg('../img/danial_nam.png', none)
  width: 50px;
  height: 50px;
  border-radius: 50%;
  float: left;
  margin-right: 10px;
}

.header--homeBtn--text{
  @include normal_transition(0.3s);
  float: left;
  font-size: 20px;
  margin-top: 22px;
  font-family: $textFont;
  color: #fff;
  cursor: default;
}

#nav{
  position: absolute;
  width: 200px;
}

#nav li{
  @include normal_transition(0.3s);
  float: left;
  color: #fff;
  width: 100px;
  text-align: center;
  line-height: 50px;
  font-size: 20px;
  font-family: $textFont;
  cursor: pointer;
  border-radius: 4px;
  &:hover{
    background-color: #666;
  }
}

.nav--active{
  background-color: darkslateblue;
}
