@import "../../variables.scss";

.common--modelParam--inputWrap{
  width: 150px;
  float: left;
  margin-bottom: 5px!important;
}

.common--modelParam--inputPrepend{
  width: 73px;
}

.common--modelParam--checkboxPrepend{
  width: 100%;
}

.common--modelParam--checkboxTextSpan{
  line-height: 35px;
  border-radius: .25em .25em 0em 0em!important;
  text-align: center;
}

.common--modelParam--input{
  width: 100px;
  font-size: 14px;
}

.common--modelParam--checkboxWrap{
  width: 100%;
  background-color: #fff;
  color: #111;
  margin-bottom: 0px;
  padding: 3px;
}

.common--modelParam--checkboxWrap:last-child{
  border-radius: 0em 0em .25em .25em;
}

.common--modelParam--checkbox{
  width: fit-content;
  float: left;
  height: 20px;
  margin: 2px;
}

.common--modelParam--checkboxText{
  font-size: 14px;
}

.common--modelParam--inputTextSpan{
  width: 100%;
  border: none;
  font-size: 11px;
  font-weight: bold;
  padding: 3px;
}

.common--modelParam--select{
  height: 35px;
  font-size: 14px;
}

.model--guideText{
  float: left;
}
