@import "../../../variables.scss";

.cluster--wrap {
    // position: absolute;
}

.graph--wrap{
    width: 960px;
    height: 600px;
    position: relative;
    left: 50%;
    margin-left: -480px;
    top: 200px;
}

.clustering--boxplot{
    left:0;
    width: 475px;
    height: 300px;
}

.clustering--histogram{
    width: 475px;
    height: 300px;
    left: 485px;
}

.clusterLabels--ul{
    position: absolute;
    width: 440px;
    // height: 300px;
    top: 120px;
    left: 50%;
    margin-left: -220px;
}

.clusterLabels--title{
    font-size: 25px;
    color: #fff;
    line-height: 25px;
    width: 100%;
    text-align: center;
    margin-top: -25px;
    margin-bottom: 10px;
}

.clusterLabels--li{
    @include normal_transition(0.3s);
    width: 30px;
    margin-left: 10px;
    float: left;
    border-radius: 4px;
    background-color: #ffffff;
    color: #333;
    text-align: center;
    line-height: 30px;
    font-size: 15px;
    cursor: pointer;
    &:hover{
        background-color: $awesome;
        color: #333;
    }
}

.clusterLabel--li--active{
    background-color: $otherId;
    color: #fff;
}