@import "../../variables.scss";

#loading--wrap{
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  z-index: 9999;
}

.loading--box{
  background-color: #fff;
  opacity: 0.8;
  border-radius: 4px;
  position: absolute;
  @include setAbsoluteCenter(80px, 80px);

}

.loading--circle{
  @include loadingCircle();
}
